@import url('https://fonts.googleapis.com/css?family=Heebo:400,700|Open+Sans:400,700');

ul {
    list-style-type: none;
    display: flex;
}

ul li {
    display: inline-block;
    padding: 30px;
}

body {
    background-color: #232946;
}

//featured post

:root {
    --color: #3c3163;
    --transition-time: 0.5s;
}

* {
    box-sizing: border-box;
}

a {
    color: inherit;
}


.cards-wrapper {
    display: grid;
    justify-content: center;
    align-items: center;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 4rem;
    padding: 4rem;
    width: fit-content;
    margin-top: 0;
    margin-left: 12%;
    //position: absolute;
}

.card {
    font-family: 'Heebo';
    --bg-filter-opacity: 0.5;
    background: grey;
    height: 20em;
    width: 15em;
    font-size: 1.5em;
    color: white;
    border-radius: 1em;
    padding: 0.5em;
    display: flex;
    align-items: flex-start;
    background-size: cover;
    background-position: center;
    //box-shadow: 0 0 5em -1em black;
    transition: all, var(--transition-time);
    position: relative;
    overflow: hidden;
    border: 10px solid #ccc;
    text-decoration: none;
    flex-direction: column;
}


.card h1 {
    margin-top: 180px;
    font-size: 1.5em;
    line-height: 1.2em;
}

.card p {
    font-size: 0.75em;
    font-family: 'Open Sans';
    margin-top: 0.2em;
    line-height: 2em;
}

.card .date {
    position: absolute;
    top: 0;
    right: 0;
    font-size: 0.75em;
    padding: 1em;
    line-height: 1em;
    opacity: .8;
}

.card:before,
.card:after {
    content: '';
    transform: scale(0);
    transform-origin: top left;
    border-radius: 50%;
    position: absolute;
    left: -50%;
    top: -50%;
    z-index: -5;
    transition: all, var(--transition-time);
    transition-timing-function: ease-in-out;
}

.card:before {
    background: #ddd;
    width: 250%;
    height: 250%;
}

.card:after {
    background: white;
    width: 200%;
    height: 200%;
}

.card:hover {
    color: var(--color);
    background-color: white;
}

.card:hover:before,
.card:hover:after {
    transform: scale(1);

}

.card-grid-space .num {
    font-size: 3em;
    margin-bottom: 1.2rem;
    margin-left: 1rem;
}

@media screen and (max-width: 1285px) {
    .cards-wrapper {
        grid-template-columns: 1fr 1fr;
    }
}

@media screen and (max-width: 900px) {
    .cards-wrapper {
        grid-template-columns: 1fr;
    }

    .info {
        justify-content: center;
    }

    .card-grid-space .num {
        margin-left: 0;
        text-align: center;
    }
}

@media screen and (max-width: 500px) {
    .cards-wrapper {
        padding: 4rem 2rem;
    }

    .card {
        max-width: calc(100vw - 4rem);
    }
}

//navigation
nav {
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #121629;
}

nav a {
    display: inline-block;
    color: #b8c1ec;
    text-align: center;
    padding: 16px;
    text-decoration: none;
}

.right {
    float: right;
}

nav a:hover {
    background-color: #d4939d;
    color: #232946;
}

.current {
    background-color: #d4939d;
    color: #232946;
}

